body {
  --color-blue: #00acdc;
  --header-hight: 60px;
  margin: 0;
  font-family: Roboto, sans-serif;
  font-weight: 500;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.logo-link {
  width: 40px;
  height: 40px;
}

.nav__list {
  align-items: center;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__list__item:not(:first-child) {
  margin-left: 20px;
}

.nav__list__item-link {
  color: #000;
  text-transform: uppercase;
  height: var(--header-hight);
  font-size: 12px;
  line-height: var(--header-hight);
  text-decoration: none;
  display: block;
  position: relative;
}

.nav__list__item-link:hover, .is-active {
  color: var(--color-blue);
}

.is-active:after {
  content: "";
  background-color: var(--color-blue);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}
